import { Component, OnInit } from '@angular/core';
import { TravelService } from '../services/travel.service';
import { FormService } from '../services/form.service';
import { ActivatedRoute } from '@angular/router';
import { VehicleService } from '../services/vehicle.service';
import { DriverService } from '../services/driver.service';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-allocation',
  templateUrl: './allocation.component.html',
  styleUrls: ['./allocation.component.scss']
})
export class AllocationComponent implements OnInit {

  protected model: any = {};
  protected isLinear = false;
  protected checked = false;
  protected terminal = false;
  protected id;

  protected availableVehicles = [];
  protected busyVehicles = [];
  protected travelsByVehicle = [];
  protected vehicles = [];
  protected vehiclesHeader = ['Veículo', 'Placa', 'Marca', 'Capacidade'];

  protected drivers = [];
  protected driversHeader = ['Nome', 'Cpf', 'Cnh', 'Registro'];

  protected selectedDriver: number;
  protected selectedVehicle: number;

  protected provisoryDateHour = '01/01/2018 ';


  constructor(private travelService: TravelService,
              private route: ActivatedRoute,
              private formService: FormService,
              private vehicleService: VehicleService,
              private driverService: DriverService,
              private snackBar: MatSnackBar,
              private router: Router) {
                 this.id = this.route.snapshot.params['id'];
               }

  ngOnInit() {
     this.formService.getById(this.id).subscribe((data) => {
        this.model = data;
        this.model.departureHour = data.departureHour.slice(11);
        this.model.returnHour = data.returnHour.slice(11);

        this.vehicleService.getVehiclesWithTravels(this.model.travelDate).subscribe((data2) => {
          for (const vehicleData of data2) {
              this.busyVehicles.push(vehicleData);
          }
        });
      }, error => {
        console.error(error);
      }
    );

    this.vehicleService.getAvailables(this.id).subscribe((data) => {
      for (const vehicleData of data) {
          this.availableVehicles.push(vehicleData);
      }
    });

    this.vehicleService.vehiclesObs.subscribe((data) => {
      for (const vehicleData of data) {
          this.vehicles.push(vehicleData);
      }
    });

    this.driverService.driversObs.subscribe((data) => {
      for (const driverData of data) {
          this.drivers.push(driverData);
      }
    });
  }

  selectDriver(id: number) {
    this.selectedDriver = id;
  }

  selectVehicle(id: number) {
    this.selectedVehicle = id;
  }

  updateForm() {
    const id = this.model.id;
    const form = Object.assign({}, this.model);
    form.travelDate += ' ' + form.departureHour;
    form.returnDate += ' ' + form.returnHour;
    form.requestDate += ' ' + form.returnHour;
    form.returnHour = form.returnDate;
    form.departureHour = form.travelDate;

    this.formService.update(id, form).subscribe((data) => {
      const snackBarRef = this.snackBar.open('Solicitação atualizada!', 'OK', { duration: 5000});
      console.log(data);
    }, error => {
      const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', { duration: 5000});
      console.log(error);
    });
  }
  confirmTravel() {
    const form = {
      formId: +this.id,
      vehicle: this.selectedVehicle,
      driver: this.selectedDriver

    };
    this.travelService.create(form).subscribe(data => {
      const snackBarRef = this.snackBar.open('Solicitação aceita e viagem confirmada!', 'OK', { duration: 5000});
      this.router.navigate(['/home/travels']);
    }, error => {
      const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', { duration: 5000});
    });
  }

  changeDeparturePoint(point: string) {
    if (point !== this.model.departurePoint) {
      this.model.departurePoint = point;
    }
  }

  removeForm(id: number) {
    this.formService.deleteById(id).subscribe(data => {
      const snackBarRef = this.snackBar.open('Formulário removido com sucesso!', 'OK', { duration: 5000});
      this.router.navigate(['/home/solicitations']);
    }, error => {
      const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', { duration: 5000});
    });
  }

  showTravels(id: number) {
    console.log(id, this.model.travelDate);
    this.travelsByVehicle = [];
    this.vehicleService.getTravelsFromVehicles(this.id, this.model.travelDate).subscribe((data) => {
      for (const vehicleData of data) {
          this.travelsByVehicle.push(vehicleData);
      }
    });
    console.log(this.travelsByVehicle);
  }
}



